// 添加关键词
<template>
    <div>
        <div class="padding">
            <ul class="list">
                <li v-for="(item, index) in listData" :key="index" ref='list'>
                    <div class="left">
                        <img src="../../assets/delete.png" @click="deleteKeyword(index)">
                        <input
                            type="text" :value='item'
                            maxlength="10"
                            @keyup="keyChangeValue($event, index)"
                            @change="changeValue($event, index)">
                    </div>
                </li>
                <li @click="addKeyword">
                    <div class="left">
                        <img src="../../assets/publish/add.png">
                        <span class="add-keyword">{{$t('personal.addKeywords')}}</span>
                    </div>
                </li>
            </ul>
        </div>
        <confirm-btn @confirmClick="confirmClick"></confirm-btn>
    </div>
</template>

<script>
import TypeList from '../../components/personal/type-list';
import ConfirmBtn from '../../components/common/confirm-button';
import { mapMutations, mapState } from 'vuex';
import { publicGet } from '../../js/api/index.js';
import getApiUrl from '../../js/api/apiUrlList';
import { chinesenumberletter } from '../../js/regexp/index.js';
export default {
    data () {
        return {
            listData: [] // 列表数据
        };
    },
    computed: {
        ...mapState({
            keywords: state => state.personal.keywords
        })
    },
    methods: {
        ...mapMutations({
            setKeywordList: 'setKeywordList'
        }),
        // 确认
        confirmClick () {
            let sum = 0;
            let isConfirm = 0;
            this.listData.forEach(ele => {
                if (ele === '') {
                    sum++;
                }
                if (!chinesenumberletter.test(ele)) {
                    isConfirm++;
                }
            });
            if (sum !== 0) {
                this.$toast.top('请输入关键字');
                return;
            }
            if (isConfirm !== 0) {
                this.$toast.top('只允许输入英文、汉字、数字');
                return;
            }
            this.setKeywordList(this.listData);
            this.$router.go(-1);
        },
        // 删除关键字
        deleteKeyword (index) {
            this.listData.splice(index, 1);
        },
        // 添加空的li
        addKeyword () {
            this.listData.push('');
        },
        keyChangeValue (e, index) {
            if (e.target.value.length >= 10) {
                this.$toast.top('每个关键词不得超过10个字');
            } else if (!chinesenumberletter.test(e.target.value)) {
                this.$toast.top('只允许输入英文、汉字、数字');
            }
            this.listData[index] = e.target.value;
        },
        // 变化监听存放在数组中
        changeValue (e, index) {
            if (!chinesenumberletter.test(e.target.value)) {
                this.$toast.top('只允许输入英文、汉字、数字');
            }
            this.listData[index] = e.target.value;
        }
    },
    created () {
        this.$router.setTitle(this.$t('title.editKeyword'));
        if (this.keywords && this.keywords.length > 0) {
            this.listData = JSON.parse(JSON.stringify(this.keywords));
        } else {
            let obj = {
                userId: this.$route.query.userId
            };
            publicGet(getApiUrl('getKeywords'), obj).then((res) => {
                this.listData = res;
            }).catch(() => {
                console.warn('error');
            });
        }
    },
    components: {
        TypeList,
        ConfirmBtn
    }
};
</script>

